import React, { useEffect, useMemo, useRef, useState } from "react";

import Spinner from "../common/spinner";

const Babylon = () => {
  const [isBabylonReady, setIsBabylonReady] = useState<boolean>(false);
  const isInitial = useRef<boolean>(true);
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    if (
      !isInitial.current ||
      !canvasRef.current ||
      typeof window == "undefined"
    )
      return;

    isInitial.current = false;

    // WORKER instance creation
    const babylonWorker = new Worker(
      new URL("../../workers/babylon.worker.ts", import.meta.url),
      {
        type: "module",
      }
    );
    babylonWorker.onmessage = ({ data }) => {
      if (data === "ready") setIsBabylonReady(true);
    };

    // OFFSCREEN canvas
    const canvas = canvasRef.current;
    canvas.width = canvas.clientWidth;
    canvas.height = canvas.clientHeight;

    const offscreenCanvas = canvas.transferControlToOffscreen();
    babylonWorker.postMessage({ canvas: offscreenCanvas }, [offscreenCanvas]);

    // RESIZE handler
    const onResizeHandler = () => {
      babylonWorker.postMessage({
        width: canvas.clientWidth,
        height: canvas.clientHeight,
      });
    };
    window.addEventListener("resize", onResizeHandler);

    // WHEEL handler
    const onWheelHandler = (e: WheelEvent) => {
      e.preventDefault();
      babylonWorker.postMessage({ wheel: e.deltaY });
    };
    canvas.addEventListener("wheel", onWheelHandler);

    // ROTATE handler
    const onMouseMoveHandler = (e: MouseEvent) => {
      babylonWorker.postMessage({
        x: e.movementX,
        y: e.movementY,
      });
    };
    canvas.addEventListener("pointerdown", (e: MouseEvent) => {
      e.preventDefault();
      window.addEventListener("pointermove", onMouseMoveHandler);
      window.addEventListener(
        "pointerup",
        () => window.removeEventListener("pointermove", onMouseMoveHandler),
        { once: true }
      );
    });

    // UNMOUNT handler
    return () => {
      if (window) {
        window.removeEventListener("resize", onResizeHandler);
        window.removeEventListener("pointermove", onMouseMoveHandler);
      }
      if (canvas) {
        canvas.removeEventListener("wheel", onWheelHandler);
      }
      babylonWorker.postMessage({ dispose: true });
    };
  }, []);

  return (
    <div className={`h-full w-full relative`}>
      <canvas
        ref={canvasRef}
        className={`bg-main-100 h-160 w-full flex items-center justify-center cursor-pointer`}
      />
      {!isBabylonReady ? (
        <Spinner className="absolute top-0 left-0 right-0 bottom-0 z-10" />
      ) : null}
    </div>
  );
};

export default Babylon;
