import React, { useEffect, useRef, useState } from "react";

import { HeadFC, useStaticQuery } from "gatsby";
import { graphql } from "gatsby";

import { IWorkflowStep } from "../types/workflow.type";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import WorkflowStep from "../components/workflow/workflowStep";

const Workflow = () => {
  const isInitial = useRef<boolean>(true);
  const [scroll, setScroll] = useState<boolean>(false);

  const scrollHandler = () => {
    setScroll((scroll) => !scroll);
  };

  useEffect(() => {
    if (!isInitial.current || !window) return;
    isInitial.current = false;

    window.addEventListener("scroll", scrollHandler);

    return () => window.removeEventListener("scroll", scrollHandler);
  }, []);

  const workflowStepsData = useStaticQuery(query);
  let workflowSteps = (
    workflowStepsData?.allMarkdownRemark?.nodes?.length
      ? workflowStepsData.allMarkdownRemark.nodes
      : null
  ) as IWorkflowStep[] | null;

  return (
    <>
      <Banner heightClass="h-136" title="workflow" text="" />
      <div className="bg-secondary-100">
        <ContentContainer className="flex-col items-start p-6 md:py-24 relative transition-all duration-1000">
          {workflowSteps
            ? workflowSteps.map((step) => (
                <WorkflowStep
                  key={step.id}
                  step={step}
                  className="even:translate-x-full even:ml-[10%]"
                  scroll={scroll}
                />
              ))
            : null}
          <div className="absolute left-1/2 top-0 bottom-0 border-r-2 border-solid border main-100 -translate-x-1/2" />
        </ContentContainer>
      </div>
    </>
  );
};

export default Workflow;

export const Head: HeadFC = () => <title>CodeCave Workflow</title>;

const query = graphql`
  query {
    allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "workflow" } } }
      sort: { frontmatter: { step: ASC } }
    ) {
      nodes {
        id
        html
        frontmatter {
          title
          image {
            publicURL
          }
        }
      }
    }
  }
`;
