import React, { useEffect, useState, useRef } from "react";

import { IWorkflowStep } from "../../types/workflow.type";

const WorkflowStep = ({
  step,
  scroll,
  className,
}: {
  step: IWorkflowStep;
  scroll: boolean;
  className?: string;
}) => {
  const div = useRef<HTMLDivElement>(null);
  const [coverWidth, setCoverWidth] = useState<"w-[105%]" | "w-0">("w-[105%]");

  useEffect(() => {
    if (!div.current || !window) return;
    const divBottom = div.current.getBoundingClientRect().bottom;

    if (window.innerHeight > divBottom) setCoverWidth("w-0");
  }, [scroll]);

  const {
    html,
    frontmatter: {
      title,
      image: { publicURL: imageURL },
    },
  } = step;

  return (
    <div
      className={`workflow-step relative w-[45%] flex flex-col h-min rounded-2xl p-10 -my-4 first:mt-0 last:mb-0 border-[3px] border-dashed border-main-100 text-main-100 cursor-pointer ${className}`}
      ref={div}
    >
      <div
        className={`cover absolute -right-2 -top-2 -bottom-2 z-10 bg-secondary-100 transition-all duration-[2000ms] ${coverWidth}`}
      />
      <div className="flex items-center pb-4">
        <img src={imageURL} alt={`${title} logo`} className="mr-7" />
        <h4 className="font-extrabold text-2xl">{title}</h4>
      </div>
      <div className={`text-xl`} dangerouslySetInnerHTML={{ __html: html }} />
      <div className="bondline" />
    </div>
  );
};

export default WorkflowStep;
